.card {
  width: 400px;
  /* Adjust the width as needed */
  height: 500px;
  /* Adjust the height as needed */
  position: relative;
}

.card-img {
  width: 100%;
  height: 200px;
  /* Adjust the height as needed */
  object-fit: cover;
}

.btn-read-more {
  position: absolute;
  bottom: 10px;
  left: 50%;
  transform: translateX(-50%);
}

.overflow-hidden {
  overflow: hidden;
  text-overflow: ellipsis;
  /* white-space: nowrap; */
}

.navbar {
  height: 100px;
}

footer {
  background-color: black;
  margin-top: 50px;
  padding: 20px 0;
  width: 100%;
}

.foot {
  padding: 0;
  box-sizing: border-box;
  background-color: black;
  width: 100%;
  bottom: 0;


}

.data-row {
  display: flex;
  justify-content: space-between;
  padding: 40px 100px;

}

.footer-col ul {
  list-style: none;
}

.footer-col h4 {
  color: white;
  margin-bottom: 25px;
  font-size: 22px;
  position: relative;
}

.footer-col h4::before {
  content: '';
  width: 70px;
  height: 2px;
  position: absolute;
  background-color: red;
  bottom: -10px;
}

.dots{
  display: flex;
  gap: .7rem;
}


@keyframes colorFade {
  0% {background-color: tomato}
  25% {background-color: rgb(0, 255, 157)}
  50% {background-color: blueviolet}
  75% {background-color: rgb(234, 0, 255)}
  100% {background-color: tomato}
}
@keyframes mycolor1 {
  from {
    background-color: red;
  }

  to {
    background-color: blue;
  }
}

.dot {
  width: 12px;
  aspect-ratio: 1;
  border-radius: 40%;
  background-color: tomato;
}


.dot.dance {
  animation: rise 1000ms infinite alternate, colorFade 2s infinite;
}

@keyframes rise {
  100% {transform: translateY(-100px) }
}

