.custom_1 {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  /* Three columns with equal width */
  /* grid-gap: 2px; Adjust the gap between columns as needed */
  row-gap: 50px;
  column-gap: 5px;
  justify-items: center;
  align-items: center;
}

.image-button {
  display: inline-block;
  cursor: pointer;
  /* Set the desired width and height for the button */
  width: 50px;
  height: 49px;
  margin-top: 3px;
  margin-left: 6px;
  margin-right: 18px;
  border-radius: 92%;
  overflow: hidden;
  transition: box-shadow 0.1s;
  /* Add transition for smooth effect */
  /* Add additional styling as needed */
}

.img_hover:hover {
  box-shadow: 0 0 20px 5px rgb(21, 0, 255);
  /* Add box-shadow on hovering */
}

.image-button img {
  /* Adjust the image size to fit within the button */
  max-width: 100%;
  max-height: 100%;
}

.container {
  position: relative;
  text-align: center;
  color: white;
}

.bottom-right {
  position: absolute;
  bottom: -16px;
  right: -116px;
  width: 30px;
  border-radius: 50%;
}